@font-face {
  font-family: "Circular";
  font-style: normal;
  font-weight: 200;
  src: url("/static/fonts/circular/circular-light.woff2") format("woff2"), url("/static/fonts/circular/circular-light.woff") format("woff"); }

@font-face {
  font-family: "Circular";
  font-style: normal;
  font-weight: 400;
  src: url("/static/fonts/circular/circular-book.woff2") format("woff2"), url("/static/fonts/circular/circular-book.woff") format("woff"); }

@font-face {
  font-family: "Circular";
  font-style: normal;
  font-weight: 600;
  src: url("/static/fonts/circular/circular-medium.woff2") format("woff2"), url("/static/fonts/circular/circular-medium.woff") format("woff"); }

@font-face {
  font-family: "Circular";
  font-style: normal;
  font-weight: 800;
  src: url("/static/fonts/circular/circular-bold.woff2") format("woff2"), url("/static/fonts/circular/circular-bold.woff") format("woff"); }

.container {
  width: calc(100% - 30px);
  max-width: 1140px;
  margin: auto; }

.wrapper {
  min-height: 100vh; }

html {
  box-sizing: border-box;
  scroll-behavior: smooth; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
li,
p,
pre,
blockquote,
figure,
hr {
  margin: 0;
  padding: 0; }

ul {
  list-style: none; }

input,
textarea,
select,
button {
  color: inherit;
  font: inherit;
  letter-spacing: inherit; }

embed,
iframe,
img,
object,
video {
  display: block;
  max-width: 100%; }

table {
  table-layout: fixed;
  width: 100%; }

[hidden] {
  display: none; }

:global(body) {
  margin: 0;
  font-family: 'Circular', -apple-system, BlinkMacSystemFont, Avenir Next, Avenir, Helvetica, sans-serif;
  background: #fafafa; }
